import React from 'react';
import '../App.css';
import { Button } from './Button';
import './HeroSection.css';
import { Link } from 'react-scroll';


function HeroSection() {
  return (
    <>
      <div className='hero-container' id='home'>
        {/* <div className='hero-background'>
          <img src='https://lasemana.com.uy/wp-content/uploads/2020/09/DSCN1682-scaled.jpg' alt='' />
        </div> */}
        <div className='container mx-auto h-full'>
          <div className='flex items-center h-full pt-8'>
            <div className='flex-1 flex flex-col items-start lg:items-start'>

              <h1 className='text-4xl leading-[44px] md:text-5xl md:leading-tight lg:text-7xl lg:leading-[1.2] font-bold md:tracking-[-2px]'>
                Proyecto <br/> Colonia
              </h1>
              <p className='text-sm bg-white text-accent text-md mb-[22px]'>
                Smate a construir un Partido Nacional participativo
              </p>
              <info></info>
              <button className='btn btn-md bg-accent hover:bg-secondary-hover md:btn-lg transition-all'>
              <Link
                to={'contact'}
                activeClass='active'
                spy={true}
                smooth={true}
                duration={500}
                offset={-70}
                className='transition-all duration-300'
              >
                Smate a la Barra
              </Link>
              </button>
            </div>
          </div>
        </div>
      </div>

    </>
  );
}

export default HeroSection;